/**
 * DevLog - 
 *
 * 
 */

import React, { useState, useEffect, useRef, useCallback } from 'react';
import {
    Terminal,
    Trash2,
    Download,
    Search,
    Filter,
    ArrowDownToLine,
} from 'lucide-react';
import { Logger, LogEntry, LogLevel, LogLevelConfig } from '../../infrastructure/logger';
import { LogEntryItem } from './LogEntryItem';

// 
const MODULES = [
    'ALL',
    'Logger',
    'EventBus',
    'CORE/Pipeline',
    'CORE/RAG',
    'CORE/Memory',
    'UI/GraphView',
    'UI/MemoryStream',
];

export const DevLog: React.FC = () => {
    const [logs, setLogs] = useState<LogEntry[]>([]);
    const [filteredLogs, setFilteredLogs] = useState<LogEntry[]>([]);
    const [searchQuery, setSearchQuery] = useState('');
    const [levelFilter, setLevelFilter] = useState<LogLevel | -1>(-1); // -1 
    const [moduleFilter, setModuleFilter] = useState('ALL');
    const [autoScroll, setAutoScroll] = useState(true);
    const [showLevelDropdown, setShowLevelDropdown] = useState(false);
    const [showModuleDropdown, setShowModuleDropdown] = useState(false);

    const terminalRef = useRef<HTMLDivElement>(null);
    const bottomRef = useRef<HTMLDivElement>(null);

    // 
    useEffect(() => {
        // 
        setLogs(Logger.getLogs());

        // 
        const unsubscribe = Logger.subscribe((entry) => {
            setLogs((prev) => [...prev, entry]);
        });

        return () => unsubscribe();
    }, []);

    // 
    useEffect(() => {
        let result = logs;

        // 
        if (levelFilter !== -1) {
            result = result.filter((log) => log.level >= levelFilter);
        }

        // 
        if (moduleFilter !== 'ALL') {
            result = result.filter((log) => log.module.startsWith(moduleFilter));
        }

        // 
        if (searchQuery.trim()) {
            const query = searchQuery.toLowerCase();
            result = result.filter(
                (log) =>
                    log.message.toLowerCase().includes(query) ||
                    log.module.toLowerCase().includes(query)
            );
        }

        setFilteredLogs(result);
    }, [logs, levelFilter, moduleFilter, searchQuery]);

    // 
    useEffect(() => {
        if (autoScroll && bottomRef.current) {
            bottomRef.current.scrollIntoView({ behavior: 'smooth' });
        }
    }, [filteredLogs, autoScroll]);

    // 
    const handleClear = useCallback(async () => {
        await Logger.clear();
        setLogs([]);
    }, []);

    // 
    const handleExport = useCallback(() => {
        const markdown = Logger.exportToMarkdown();
        const filename = Logger.getExportFilename();

        const blob = new Blob([markdown], { type: 'text/markdown' });
        const url = URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = filename;
        a.click();
        URL.revokeObjectURL(url);

        Logger.success('DevLog', `: ${filename}`);
    }, []);

    return (
        <div className="engram-dev-log">
            {/*  */}
            <div className="engram-page-header">
                <Terminal size={24} />
                <h2></h2>
            </div>

            {/*  */}
            <div className="engram-log-toolbar">
                {/*  */}
                <div className="engram-dropdown">
                    <button
                        className="engram-btn engram-btn-ghost"
                        onClick={() => setShowLevelDropdown(!showLevelDropdown)}
                    >
                        <Filter size={14} />
                        {levelFilter === -1 ? '' : LogLevelConfig[levelFilter].label}
                    </button>
                    {showLevelDropdown && (
                        <div className="engram-dropdown-menu">
                            <button
                                onClick={() => {
                                    setLevelFilter(-1);
                                    setShowLevelDropdown(false);
                                }}
                            >
                                
                            </button>
                            {Object.entries(LogLevelConfig).map(([level, config]) => (
                                <button
                                    key={level}
                                    onClick={() => {
                                        setLevelFilter(Number(level) as LogLevel);
                                        setShowLevelDropdown(false);
                                    }}
                                >
                                    {config.icon} {config.label}
                                </button>
                            ))}
                        </div>
                    )}
                </div>

                {/*  */}
                <div className="engram-dropdown">
                    <button
                        className="engram-btn engram-btn-ghost"
                        onClick={() => setShowModuleDropdown(!showModuleDropdown)}
                    >
                        <Filter size={14} />
                        {moduleFilter}
                    </button>
                    {showModuleDropdown && (
                        <div className="engram-dropdown-menu">
                            {MODULES.map((mod) => (
                                <button
                                    key={mod}
                                    onClick={() => {
                                        setModuleFilter(mod);
                                        setShowModuleDropdown(false);
                                    }}
                                >
                                    {mod}
                                </button>
                            ))}
                        </div>
                    )}
                </div>

                {/*  */}
                <div className="engram-search-box">
                    <Search size={14} />
                    <input
                        type="text"
                        placeholder="..."
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                    />
                </div>

                {/*  */}
                <div className="engram-toolbar-right">
                    {/*  */}
                    <button
                        className={`engram-btn engram-btn-ghost ${autoScroll ? 'active' : ''}`}
                        onClick={() => setAutoScroll(!autoScroll)}
                        title=""
                    >
                        <ArrowDownToLine size={14} />
                    </button>

                    {/*  */}
                    <button
                        className="engram-btn engram-btn-ghost"
                        onClick={handleClear}
                        title=""
                    >
                        <Trash2 size={14} />
                    </button>

                    {/*  */}
                    <button
                        className="engram-btn engram-btn-primary"
                        onClick={handleExport}
                        title=""
                    >
                        <Download size={14} />
                        
                    </button>
                </div>
            </div>

            {/*  */}
            <div className="engram-terminal" ref={terminalRef}>
                {filteredLogs.length === 0 ? (
                    <div className="engram-terminal-empty">
                        <Terminal size={48} strokeWidth={1} />
                        <p></p>
                    </div>
                ) : (
                    <>
                        {filteredLogs.map((entry) => (
                            <LogEntryItem key={entry.id} entry={entry} />
                        ))}
                        <div ref={bottomRef} />
                    </>
                )}
            </div>

            {/*  */}
            <div className="engram-log-statusbar">
                <span> {logs.length} </span>
                {filteredLogs.length !== logs.length && (
                    <span> {filteredLogs.length} </span>
                )}
            </div>
        </div>
    );
};

export default DevLog;
